import React, { useState, useEffect } from 'react';
import { Box, Typography } from '@mui/material';

const Home: React.FC = () => {
  const [circleIndex, setCircleIndex] = useState<number>(0);

  const circles: string[] = ['Hallo', 'Willkommen', 'Erkunden', 'Loslegen'];

  // Scroll-Event-Handler
  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      const newIndex = Math.min(Math.floor(scrollPosition / 200), circles.length - 1); // Alle 200px wird ein neuer Kreis angezeigt
      setCircleIndex(newIndex);
      console.log(scrollPosition);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [circles.length]);

  return (
    <Box display="flex" flexDirection="column" alignItems="center" mt={4} minHeight="400vh" pt={4}>
      {circles.map((text, index) => (
        <Box key={index} position="relative" textAlign="center" mt={8}>
          {index <= circleIndex && (
            <Box
              sx={{
                width: '80px',
                height: '80px',
                borderRadius: '50%',
                backgroundColor: 'primary.main',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                color: 'white',
                zIndex: 10,
              }}
            >
              <Typography variant="h6">{text}</Typography>
            </Box>
          )}
          {index < circleIndex && (
            <Box
              sx={{
                width: '2px',
                height: '100px',
                backgroundColor: 'black',
                position: 'absolute',
                top: '80px',
                left: '50%',
                transform: 'translateX(-50%)',
              }}
            />
          )}
        </Box>
      ))}
    </Box>
  );
};

export default Home;
