import React from 'react';
import { Box, Typography } from '@mui/material';

const Home: React.FC = () => {
  const circles = ['Hallo', 'Willkommen', 'Erkunden', 'Loslegen'];

  return (
    <Box display="flex" flexDirection="column" alignItems="center" mt={4}>
      {circles.map((text, index) => (
        <Box
          key={index}
          display="flex"
          flexDirection="column"
          alignItems="center"
          justifyContent="center"
          height="100vh" // Jeder Abschnitt fllt die volle Hhe des Bildschirms
          sx={{
            scrollSnapAlign: 'start', // Damit jeder Abschnitt beim Scrollen schnappt
          }}
        >
          {/* Kreis */}
          <Box
            sx={{
              width: '80px',
              height: '80px',
              borderRadius: '50%',
              backgroundColor: 'primary.main',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              color: 'white',
              zIndex: 10,
            }}
          >
            <Typography variant="h6">{text}</Typography>
          </Box>

          {/* Linie fr alle auer den letzten Kreis */}
          {index < circles.length - 1 && (
            <Box
              sx={{
                width: '2px',
                height: '100px',
                backgroundColor: 'black',
                mt: 2,
              }}
            />
          )}
        </Box>
      ))}
    </Box>
  );
};

export default Home;
